
    import { defineComponent, ref, computed } from 'vue'
    import { useStore } from "@/store/index";

    export default defineComponent({
        // 型推論を有効にする
        name: 'ResultViewer',
        setup(){
            const store = useStore();
            const ResultItems = computed(() => store.state.ResultItems);

            return { ResultItems }
        }
    })

